@charset "UTF-8";
/* A Modern CSS Reset */
/* https://coliss.com/articles/build-websites/operation/css/css-reset-for-modern-browser.html */
*, *::before, *::after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
}

ul[role=list], ol[role=list], li {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, *::before, *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: "游明朝体", "Yu Mincho", YuMincho, serif;
}

.wrapper {
  display: flex;
}

.main {
  padding: 120px 80px 80px;
}

h1 {
  color: green;
}
@media screen and (max-width: 768px) {
  h1 {
    color: darkgreen;
  }
}
h1 span {
  display: flex;
  align-items: center;
  position: relative;
}

.header {
  background-color: #fff;
  border-bottom: solid 1px #d40000;
}

.nav {
  max-width: 1200px;
  margin: 0 auto;
}
.nav_list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.nav_item {
  display: block;
}
.nav_item + .nav_item {
  margin-left: 16px;
}
.nav_item a {
  font-size: 24px;
  font-weight: bold;
  color: #777;
  transition: all 0.3s;
  text-decoration: none;
}
.nav_item a:hover {
  text-decoration: underline;
}
.nav_item-first {
  margin-right: auto;
}

.aside {
  background-image: linear-gradient(to bottom, #ffffc6 0%, #ffdd74 100%);
  border-right: solid 1px #d40000;
  height: 100vh;
  width: 240px;
  max-width: 240px;
}

